
.play{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    padding: 5px;
    margin:auto;
    cursor: pointer;
    background-color: green;
    border: none;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    }
    .play:hover{
      background-color: rgb(56, 228, 56);
      color: white;
      border-radius: 50%;
    }
    .play:active{
      border: 5px solid white;
      border-radius: 50%;
    }
@media(max-width:500px){
    .play{
      margin: auto;
    }

  }
.score{
  z-index: 1;
  position: absolute;
  bottom: 10px;
  text-align: center;
  font-size: 20px;
  width: 60%;
  left: 20%;
}
.score p {
  float: left;
  margin: 3px 5px;
}
.dashboardBtn{
  height: 5vh;
  cursor: pointer;
  background-color: rgb(41, 129, 41);
  border: none;
  border-radius: 5px;
  margin: 0 5px;
  float: right;
}
.dashboardBtn:hover{
  background-color: rgb(81, 255, 0);
  color: rgb(221, 53, 2);
}