body{
    background-color: green;
}
.users h1{
    margin:10px auto;
    width:200px;
/* background:silver; */
}
.users ul{
    margin:0 auto;
    width:50vh;
    font-size:30px;
    font-weight:bold;
    list-style-type:none;
}
.users li{
    padding: 5px;
    position:relative;
    border:1px solid black;
}
.users li p {
    right:15px;
    top:-30px;
    position:absolute;

}
.users li:nth-child(odd){
    background:silver;
}
.users li:nth-child(1){
      color: gold;
  }
  .users li:nth-child(2){
      color: silver;
  }
  .users li:nth-child(3){
      color: #CD7F32;
  }
  @media(max-width:500px){
    .users ul{
        margin-right: 6vh;
        width:90%;
        font-size:20px;
        font-weight:normal;
    }
    .users li p{
        top: -15px;
    }
  }