.gameover{
    box-sizing: border-box;
    margin: 20vh 0;
    padding: 20px;
    background-color: red;
    /* height: 50vh; */
    width: 100%;
    /* font-size: 30px; */
position: absolute;
z-index: 1;
  }
  .gameover button{
    background-color: royalblue;
    border-radius: 10px;
    height: 30px;
    margin: 10px auto;
    border: none;
cursor: pointer;  
}
  .gameover button:hover{
    background-color: slateblue;
    color: white;
    /* z-index: auto; */
  }

  @media(max-width:500px){
 
    .gameover{
      width: 100%;
    }

  }