.info{
    position: absolute;
    width: 100%;
    height: 50vh;
    z-index: 2;
    margin: 0 auto;
    background-color: aqua;
  }
  .scores{
    width: 150px;
    margin: 0 auto;
    height: 15vh;
    position: relative;
    text-align: left;
  }
  .scores li{
      margin: -10px;
  }
  .scores span{
      float: right;
  }

 .scores li:nth-child(1){
      color: gold;
  }
  .scores li:nth-child(2){
      color: silver;
      text-decoration: none;
  }
  .scores li:nth-child(3){
      color: #CD7F32;
  }
  
#oops{
    color:red;
    text-align:justify;
    width:80%;
    margin: 10px auto;
}
.guest{
    position: absolute;
    top: 35vh;
    margin: auto ;
    width: 100%;
}
.info p{
    width: 100% ;
