.menu{
    position: absolute;
    width: 100%;
    height: 50vh;
    z-index: 2;
    top:50vh;
    background-color: aqua;
    /* background-color: red; */
}
.logout{
    margin:5%;
    height: 5vh;
    width: 15vh;
    cursor: pointer;
    background-color: rgb(211, 81, 81);
    border-radius: 5px;
    box-shadow: 2px 2px 3px black;

  }
  .logout:hover{
    background-color: rgb(255, 19, 19);
  }
  .logIn{
    margin: 5%;
    height: 5vh;
    width: 15vh;
    cursor: pointer;
    background-color: rgb(119, 236, 119);
    border-radius: 5px;
    box-shadow: 2px 2px 3px black;

  }
  .logIn:hover{
    background-color: rgb(42, 233, 42);
  }
 
  .backToPlay{
    width: 15vh;
    margin:5%;
    height: 5vh;
    cursor: pointer;
    background-color: rgb(107, 233, 107);
    border-radius: 5px;
    box-shadow: 2px 2px 3px black;

  }
  .backToGame:hover{
    background-color: rgb(0, 185, 0);
  }
  .muteMode{
    margin:5%;
    width: 15vh;
    height: 5vh;
    cursor: pointer;
    background-color: rgba(0, 0, 85, 0);
    border-radius: 5px;
    border: none;
    box-shadow: 2px 2px 3px black;
  }
  .muteMode:hover{
    box-shadow:2px 2px 3px 2px black;
  }
  .tip {
    padding: 10px;
    text-align: justify;
    color: green;
  }