.form{
    position: absolute;
    width: 100%;
    height: 50vh;
    z-index: 3;
    top:50vh;
    background-color: aqua;
}
.inputName{
    width:250px ;
    margin: 0 auto;
  }
input{
  margin:10px auto;
  width:200px ;
  top:32vh;
  left: 25%;
  border: none;
  background-color: white;
  height: 6vh;
  border-radius: 5px;
}
.inputName button{
  width: 200px;
  height: 5vh;
  cursor: pointer;
  background-color: green;
  border-radius: 5px;
}
.inputName button:hover{
  background-color: chartreuse;
  color: rgb(221, 53, 2);
}
.warn{
  color: red;
  margin: 5px auto  ;
  /* width: 50vh; */
  text-align: left;
  /* background-color: green; */
  text-align: center;
}
@media(max-width:500px){
  input{
    width:90% ;
  }
  .inputName button{
    width:90% ;
  }
}
.form p{
  margin: 0 auto;
  /* width: 300px; */
}
.form .guest{
  /* z-index: 5; */
  position: absolute;
}